.slideshow {
  font-family: "Myriad Pro";
}
.slideshow__container {
  width: 100%;
  height: 100%;
  background-image: url("/img/bg.png");
  background-repeat: repeatY;
  background-size: 100%;
  padding-top: 120px;
  z-index: -1;
}
.slideshow__control-left,
.slideshow__control-right {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.slideshow__control-left img,
.slideshow__control-right img {
  width: 80px;
  height: 80px;
  cursor: pointer;
}
.slideshow__control-left {
  transform: scaleX(-1);
}
.slideshow__content {
  display: flex;
  flex-direction: column;
}
.slideshow__content-top,
.slideshow__content-bottom {
  flex: 1;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}
.slideshow__content-top {
  justify-content: center;
}
.slideshow__header {
  font-size: 41px;
  color: #28363d;
  margin-top: 20px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
}
.slideshow__divider {
  display: flex;
  margin: 40px 0 40px 0;
}
.slideshow__divider hr {
  background-color: #28363d;
  height: 2px;
  width: 400px;
}
.slideshow__divider img {
  margin: 0 5px 0 5px;
}
.slideshow__item {
  font-size: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #28363d;
  transform: translateZ(0) scale(0);
  transition-duration: 0.5s;
  transition-property: transform, opacity;
}
.slideshow__item.hide {
  transform: translateZ(0) scale(0);
  transition-duration: 0.5s;
  transition-property: transform, opacity;
}
.slideshow__item.show {
  transform: translateZ(0) scale(1);
}
.slideshow__btn-request {
  width: 400px;
  height: 80px;
  border-radius: 10px;
  color: #fff;
  background-color: #ff8207;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1.2;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  cursor: pointer;
  z-index: 2;
}
.slideshow__dots {
  text-align: center;
  display: flex;
  justify-content: center;
  z-index: 1;
}
.slideshow__dots-element {
  cursor: pointer;
  height: 18px;
  width: 18px;
  margin: 0 2px;
  background-color: #28363d;
  border-radius: 50%;
  z-index: 186;
}
.slideshow__dots-element.active {
  background-color: #ff8207;
}
.slideshow__btn-down {
  display: flex;
  width: 54px;
  height: 54px;
  opacity: 0.6;
  border-radius: 50%;
  background-color: #28363d;
  cursor: pointer;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
}
/* Tbalet */
@media only screen and (min-width: $tablet-min) and (max-width: $tablet-max) {
  .slideshow__divider {
    margin: 0 0 20px 0;
  }
  .slideshow__btn-request {
    margin-top: 50px;
    width: 250px;
    height: 50px;
  }
}
/* Mobile */
@media only screen and (min-width: $mobile-min) and (max-width: $mobile-max) {
  .slideshow__header {
    margin-top: 40px;
    font-size: 22px;
  }
  .slideshow__divider {
    margin: 0 0 20px 0;
  }
  .slideshow__content {
    width: 70%;
  }
  .slideshow__control-left img,
  .slideshow__control-right img {
    width: 30px;
    height: 30px;
  }
  .slideshow__btn-request {
    margin-top: 50px;
    width: 200px;
    height: 50px;
  }
}
